                'Para provar a mim que voc  veloz, ter que me enfrentar e passar pelo meu desafio. Assim, os desafios sempre vo estar disponveis, maaaaas se voc no estiver bem treinado sempre vai comer poeira, ento se esforce, treine, trabalhe duro e quem sabe um dia se torne digno(a) de combater a Ignorncia',
