import monkey from "../../../assets/sprites/monkey/monkey.png";

const chatScript = (ignorance: number) => {
    const script = [
        {
            name: "Babuno",
            image: monkey,
            texts: [`Atualmente o seu nvel de sabedoria  ${100 - ignorance}%`, `Isso significa que o nvel de ignorncia est em ${ignorance}%, mantenha-se forte na sua jornada!`]
        }
    ]
    return script;
}

export default chatScript;
